
















































import { Component, Prop, Vue } from 'vue-property-decorator';

import team from '@/../public/json/team.json';

import ArticleParagraph from '@/components/ArticleParagraph.vue';

@Component({
    components: {
        ArticleParagraph
    }
})
export default class Team extends Vue {
    private team = team;

    private getTeamImg (name: string): string {
        switch (name) {
            case '總召組':
                return 'gen';
            case '行政組':
                return 'admin';
            case '議程組':
                return 'agenda';
            case '議程助理':
                return 'supage';
            case '行銷組':
                return 'pr';
            case '場務組':
                return 'service';
            case '文創組':
                return 'design';
            case '財務組':
                return 'fin';
            case '開發組':
                return 'dev';
            case '編輯組':
                return 'edit';
            case '攝影組':
                return 'photo';
            default:
                return 'dev';
        }
    }
}
